

.process-wrap {
    background-color: #238A59;
    z-index: 1;
    padding: 70px 0 40px;
    @include media-only ("xs", min) {
        padding: 90px 0 185px;
    }
    .map-shape {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
    .section-heading {
        @include media-only ("xs", min) {
            margin-bottom: 120px;
        }
        .heading-title {
            color: $color-white;
        }
        p {
            color: $color-white;
        }
    }
}

.process-wrap-2 {
    padding: 120px 0 0 0;
    z-index: 1;
    .shape {
        position: absolute;
        top: -100%;
        left: 0;
        z-index: -1;
    }
}

.subTitle {
    color: $color-primary;
    text-align: center;
    font-family: $font-title;
    font-size: 15px;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    background-color: #F7F6FF;
    padding: 8px 15px;
    display: inline-block;
}


.process-list-wrap {
    position: relative;
    z-index: 1;
    &::after {
        content: "";
        height: 85%;
        width: 100%;
        border-left: 1px dashed #c5c5c5;
        position: absolute;
        top: 0;
        left: 30px;
        z-index: -1;
    }
    li {
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
            .process-listing {
                .content {
                    border-bottom: 0;
                    padding-bottom: 0;
                }
            }
        }
        .process-listing {
            gap: 40px;
            &:hover {
                .icon {
                    transform: scale(1);
                    background-color: $color-primary;
                    svg {
                        color: $color-white;
                    }
                }
            }
            .icon {
                height: 70px;
                width: 70px;
                border-radius: 70px;
                background-color: #F8F8FF;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                transform: scale(.9);
                transition: all .3s ease-in-out;
                svg {
                    color: $color-primary;
                    transition: all .3s ease-in-out;
                }
            }
            .content {
                flex: 1;
                border-bottom: 1px solid #DFDFDF;
                padding-bottom: 30px;
                .title {
                    font-size: 22px;
                    font-weight: 600;
                    a {
                        color: $color-black;
                    }
                }
            }
        }
    }
}

.process-dash-line {
    .curve-shape {
        display: none;
        @include media-only ("xs", min) {
            display: block;
        }
    }
    .element { 
        li {
            position: inherit;
            margin-bottom: 30px;
            @include media-only ("xs", min) {
                position: absolute;
                margin-bottom: 0;
            }
            .text {
                color: $color-white;
                font-family: $font-title;
                font-size: 20px;
                font-weight: 600;
            }
            &:nth-child(1) {
                bottom: -55px;
                left: 28px;
                @include media-only ("sm", min) {
                    bottom: -50px;
                    left: 50px;
                }
                @include media-only ("md", min) {
                    bottom: -40px;
                    left: 75px;
                }
                @include media-only ("lg", min) {
                    bottom: -35px;
                    left: 216px;
                }
            }
            &:nth-child(2) {
                top: -90px;
                left: 25%;
                @include media-only ("sm", min) {
                    left: 27%;
                }
                @include media-only ("md", min) {
                    left: 29%;
                }
                @include media-only ("lg", min) {
                    left: 33%;
                }
            }
            &:nth-child(3) {
                bottom: -90px;
                left: 46%;
                @include media-only ("sm", min) {
                    left: 47%;
                }
                @include media-only ("md", min) {
                    left: 49%;
                }
                @include media-only ("lg", min) {
                    left: 50%;
                }
            }
            &:nth-child(4) {
                top: -80px;
                left: 55%;
                @include media-only ("sm", min) {
                    left: 58%;
                }
                @include media-only ("md", min) {
                    left: 60%;
                }
                @include media-only ("lg", min) {
                    left: 59%;
                }
            } 
            &:nth-child(5) {
                bottom: -60px;
                left: 78%;
                @include media-only ("md", min) {
                    left: 80%;
                }
                @include media-only ("lg", min) {
                    left: 76%;
                }
            }       
        }
    }
}
